@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500&display=swap');
body {
  font-family: 'Ubuntu', sans-serif;
  background-color: #ffd200;
  overflow-y: hidden;
  overflow-x: hidden;
  height: 100vh;
  padding: 0;
  margin: 0;
  width: 100vw;
}


.App-logo {
  height: 10vmin;
  margin-left: -2vmin;
 }

.App-logo2{
  height: 10vmin;
  margin-left: -0.5vmin;
}

.App-header {
  background-color: #ffd200;
  min-height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  flex-wrap: wrap;
  overflow-x: hidden !important;
 
}
.App-header2 {
  margin-left: 10vmin;
  margin-top: 6vmin;
 
}
.div{
 
  display: none;
}
.divstext{
  color: #ffd200;
  display: none;
}
.text_bold{
  font-weight: 500;
}
.text_italics{
 
 font-style: italic;
}
.text{
  font-size: calc(10px + 1.6vmin);
  color: #4744c4;
 text-align: left;
 margin-top: 5vmin;
 font-weight: normal;
line-height: 130%;
font-weight: 300;
}
.text2{
  font-size: calc(10px + 0.5vmin);
  color: #4744c4;
 text-align: left;
 margin-top: 10vmin;
 font-weight: 200;


}

.text3{
  font-size: calc(10px + 4.5vmin);
  color: #4744c4;
  line-height: 100%;
}


.whiteDiv{
height:85vmin;
width: 100%;
 background-color: white;
 border-radius:3vmin ;
 margin-top: 7vmin;
 margin-right: 7vmin;
 padding-top: 1.5vmin;
 padding-left: 8vmin;
 display: flex;
}
.hands{
  height:90vmin;
  width: 115vmin; 
  position: absolute;
  top: 17%;
  transform: rotate(-30deg);
  z-index: 1;
  
}
.whiteDivContainer{
  padding: auto;
  margin-right: 9%;
  width: 50%;
  

}
